import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../components/Home";

Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      auth: true,
      title: "Home Page",
      icon: "",
    },
  },

  {
    path: "/agent",
    name: "Agent",
    meta: {
      auth: true,
      title: "Agent - connect with clients",
      icon: "agent",
    },
    component: () =>
      import(/* webpackChunkName: "agent" */ "../components/Agent.vue"),
  },
  {
    path: "/client",
    name: "Client",
    meta: {
      auth: true,
      title: "Client - connect with agents",
      icon: "client",
    },
    component: () =>
      import(/* webpackChunkName: "client" */ "../components/Client.vue"),
  },
];

let router = null;
router = new VueRouter({
  routes,
  mode: "history",
});

export default router;
